body {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    background-color: #f0f0f0; /* Set a background color for the entire body */
  }
  
  /* Container for the entire recipes page */
  .recipes-page {
    max-width: 800px;
    width: 100%;
    padding: 20px;
    box-sizing: border-box; /* Include padding and border in the total width */
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
    
  /* Styling for the main heading */
  h1 {
    text-align: center;
    color: #333;
  }
  
  /* Styling for the search form */
  form {
    margin-bottom: 20px;
  }
  
  /* Styling for form labels */
  label {
    display: block;
    margin-bottom: 5px;
    color: #555;
  }
  
  /* Styling for the search input */
  input {
    width: 100%;
    padding: 12px;
    margin-bottom: 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  /* Styling for the search button */
  button {
    padding: 12px 20px;
    background-color: #4caf50;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #45a049;
  }
  
  /* Styling for individual recipe containers */
  .recipe {
    border: 1px solid #ddd;
    padding: 20px;
    margin-bottom: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    align-items: center;
  }
  
  /* Styling for recipe headings */
  h2 {
    color: #333;
  }
  
  /* Styling for recipe images */
  img {
    max-width: 100%;
    height: auto;
    
    margin-bottom: 15px;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  /* Styling for ingredient headings */
  h3 {
    color: #555;
  }
  
  /* Styling for ingredient list */
  ul {
    list-style: none;
    padding: 0;
  }
  
  /* Styling for individual list items */
  li {
    margin-bottom: 8px;
    color: #666;
  }
  
  /* Styling for the "Back to Home" link */
  .link-back {
    display: block;
    margin-top: 20px;
    color: #007bff;
    text-decoration: none;
    font-weight: bold;
    transition: color 0.3s;
  }
  
  .link-back:hover {
    color: #0056b3;
  }
  